//1.导入路由插件
import VueRouter from 'vue-router'
//2.导入vue插件
import Vue from 'vue'
//3.导入路由的配置信息
import config from './config.js'

//重写VueRouter中的方法，捕获相同路由历史记录的异常
const stylePush=VueRouter.prototype.push;
VueRouter.prototype.push=function push(location){
	return stylePush.call(this,location).catch(err=>err)
}
//4.使用
Vue.use(VueRouter);
//5.创建路由实例 并加载配置信息
const router=new VueRouter(config);

//6.导出设置
export default router;
