/*store配置vuex*/

/*1.导入vue*/
import Vue from 'vue'
/*2.导入vuex*/
import Vuex from 'vuex'

/*3.使用vuex*/
Vue.use(Vuex);

/*4.创建vuex实例*/
const store=new Vuex.Store({
	state:{
		id:'',/* 对应榜单的id */
		trackIds:[],/* 获取到的对应榜单的歌单的trackIds */
		ids:[],/* 把trackIds中的id拿出来,放入ids数组中 */
		songIndex:'',/* 点击的歌曲名对应的id值 */
		sheetId:'',/*歌单*/
		phone: "",
		password: "",
		checkPass:"",
		mvUrl:"",
		mulist:[],
		musicUrl:"",
		singerId:'',
		mvUrl2:'',
		
	},
	mutations:{
		//第一个参数是需要修改的数据对象 之后的参数是Rangking组件中传递过来的
		//id是接收的commit提交的对象
		change(state,id){
			state.id=id;
		},
		addTrackIds(state,ids){
			state.trackIds=ids;
		},
		addIds(state,tids){
			state.ids=tids;
		},
		addSongIndex(state,songIndex){
			state.songIndex=songIndex;
		},
		findId(state,payload){
			state.sheetId = payload;
		},
		login(state,[phone,password,checkPass]){//date是接收的commit提交的整个对象
			state.phone=phone;
			state.password=password;
			state.checkPass=checkPass;
		},
		changeMv(state,mvUrl){
			state.mvUrl=mvUrl;
		},
		addMuLis(state,mulist){
			state.mulist=mulist;
		},
		musicUrl(state,murl){
			state.musicUrl=murl;
		},
		singerId(state,id){
			state.singerId=id;
		},
		changeMv2(state,mvUrl2){
			state.mvUrl2=mvUrl2;
		},
		
	},
});

/*5.导出(整体的默认导出)*/
export default store;

 //vuex仓库中的状态，一旦发生改变，那么应用它的所有组件中的状态也随之改变
 /*6、 在main.js里面导入 import store from './store'*/
 
